import globalVars from "../../../library/globalVariables";

const WebFormSelect = ({webFields, addedToForm}) => {

    const fieldsOptions = webFields.map(field => {
        return !field.fieldInForm ? <option value={field.fieldName}>{field.fieldName}</option> : null;
    })

    return (
        <>  
        <select name="fields" onChange={addedToForm}>
        <option>{globalVars.blankOption}</option>
        {fieldsOptions}
        </select>
        </>
    )
}

export default WebFormSelect;